import React, { Component } from "react";

class Author extends Component {
  render() {
    return (
      <div className="TitleDiv">
        <h1>Author</h1>
        <h4>
          Jordan Bromley is a full stack, 24 year old developer.
          <br />
          Trained in the MERN stack, he is looking to learn more to expand his
          knowledge and career opportunities.
          <br /> In his spare time, when he isn't coding, he enjoys leisurely
          activites which include :
        </h4>{" "}
        <ul>
          <li>Swimming</li> <li>Cycling</li> <li>Walking</li>{" "}
          <li>The odd live night</li> <li>A bit of gaming</li>
        </ul>
        <h4>
          {" "}
          Here is a link to his CV. Here is a link to his github Here is a link
          to his twitter. Feel free to contact anytime and hopefully a swift
          reply shall follow.
        </h4>
      </div>
    );
  }
}

export default Author;



